<div class="school_name text" >
sd
	
</div>

<div class="jigou text">
	
	{{list.school_id}}
</div>

<div class="high text">
	
	{{list.high}}
</div>
<div class="name">
	{{list.name}}
</div>


<div class="congratulation">
	
	<p v-for="item in list.up"><span>{{item.user.nickname}}</span> 为您送上最真挚的祝贺!</p>
	

</div>
<div class="num">
	祝贺人数： {{list.count}}
</div>



<img @click="congratulation" src="/static/images/share_conguatulation.png" class="share_conguatulation" alt="">

  <router-link to="/offer/myOffer" slot="left">
<img src="/static/images/share_myOffer.png" class="share_myOffer" alt="">
</router-link>

  <router-link to="/offer/form" slot="left">
<img src="/static/images/share_form.png" class="share_form" alt="">
</router-link>

</div>


</div>
</template>

<script>

import wx from 'weixin-js-sdk'
export default {
  props: ['itens'],
   created () {

  	

  	this.share_id=this.$route.query.share_id;
  	console.log(this.share_id);
  	this.index(this.share_id);


//http://api.megoal.org/oAuth/get_jssdk_ticket?url=www.baidu.com&state=universityadmintest.migao.me,snsapi_userinfo

this.wxInfoShare();




  },
  data(){
	
    return {

    	share_id:0,
    	list:[],
    	

    }

},
  methods: {

  	index:function(id){

  		 this.$http.get(domain+'api/offer/getShare?id='+id).then((response) => {
      response = response.body;
     


      this.list=response;


   
    })
  	},


        wxInfoShare:function(id){

          console.log('http://api.megoal.org/oAuth/get_jssdk_ticket?url='+domain+'offer&state='+domain+'offer/,snsapi_userinfo,/offer/share?share_id=12');


       this.$http.get('http://api.megoal.org/oAuth/get_jssdk_ticket?url='+domain+'offer/%23/offer/share?share_id='+this.share_id+'&state='+domain+'offer/,snsapi_userinfo,/offer/share?share_id=12').then((response) => {
      response = JSON.parse(response.body);

     console.log(response.timestamp);


           wx.config({
    appId: response.appid,
    timestamp: response.timestamp,
    nonceStr: response.nonsence,
    signature: response.signature,
    jsApiList: [
        'checkJsApi',
        'onMenuShareTimeline',
        'onMenuShareAppMessage',
        'onMenuShareQQ',
        'onMenuShareWeibo',
      ]
  });
wx.ready(function () {
     var shareData = {
    title: '更专业、更公开、更高效',
    desc: '晒出你的OFFER，让更多人尖叫', 
    link: response.share_url,
    imgUrl: 'http://roselycheecone.nimads.com/share.png'
  };
     wx.onMenuShareAppMessage(shareData);
     wx.onMenuShareTimeline(shareData);
     wx.onMenuShareQQ(shareData);
     wx.onMenuShareWeibo(shareData);
});



   
    })
    },




  	congratulation:function(){



  		  		 this.$http.get(domain+'api/offer/up?id='+this.share_id).then((response) => {



  	       	alert('感谢您的支持');



  		  		 });

  		  		 this.index(this.share_id);



  	}
  }

}
</script>

