<mt-field label="毕业高中（全程）" placeholder="未填写" type="text" v-model="list.high"></mt-field>


  
    <mt-search autofocus v-model="value" :result="filterResult"></mt-search>



<mt-field label="毕业学校（全程）" placeholder="未填写" type="text" v-model="list.college_id"></mt-field>

