    <p>毕业高中：{{this.list.high}}</p>
    <p>毕业学校：米高大学</p>

    <p>申请结果：录取</p>
    <p>顾问机构：吃成培训</p>
    <p>指导老师：wang</p>

      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
        <button type="button" class="btn btn-primary">确认提交</button>
      </div>
    </div>
  </div>
</div>


</div>
</template>

<script>
export default {
  props: ['itens'],
   computed: {
    filterResult() {
      return this.defaultResult.filter(value => new RegExp(this.value, 'i').test(value));
    }
  },
      data:function() {
    return {     value: '',
      defaultResult: [
        '米高大学',
        '米高小学',
        '米高',
        '北京大学',
  
      ],

    list:{},
    identity:1,
    result:1,
    data:{},
    
   }
},
       methods:{


        modealCheck:function(){

console.log(this.list);
this.data=this.list;
$('#myModal').modal('show');

        },
        create: function() {
   this.update();
      console.log(this.list);

    },
    checkIdentity:function(val){

     this.identity=val;
      console.log(this.identity);
    },
    checkResult:function(val){
      this.result=val;
    },

    back:function(){
    this.$router.go(-1);
},

share:function(id){

    this.$router.push('/offer/share?share_id='+id)


},
    update:function(){

var  input=this.list;

input.identity=this.identity;
input.result=this.result;



  this.$http.get(domain+'api/offer/uploadInfo',{ params: {data:input } }).then(function (response) {
  

  console.log(response.data);


  if(response.data.rs){

 
var id=response.data.data.id;
  this.share(id);
  }else{
  alert('上传失败');

  }
  //loading=false;

  });

    }
}
}

</script>


<style>
	.mint-header {
    -ms-flex-align: center;
