    padding-left: 0px;
        background-image: inherit;

}  .page-search {
    height: 100%;
  }
.a{
  color: #ddd
}
.h4_padd{
   padding-top:10px;
}

.button_check{
      border: 1px solid #2c91d7;
    color: #2c91d7;
}

.btn-default:hover, .btn-default:focus, .btn-default.focus, .btn-default:active, .btn-default.active, .open>.dropdown-toggle.btn-default {
